import React, { Component, PropTypes } from 'react'

class ProjectStep extends React.Component {
  render() {
    return (
      <div>
        <p>Mon projet</p>
        <fieldset className="form-group">
          <label>Nom de mon projet</label>
          <input type="text" className="form-control" placeholder="projet"/>
          <small className="text-muted">Le nom n'est pas obligatoire.</small>
        </fieldset>
        <fieldset className="form-group">
          <label>Rsum de mon projet</label>
          <textarea rows="5" className="form-control"/>
          <small className="text-muted">Non confidientiel, ne devant pas comporter d'lments senssibles</small>
        </fieldset>
        <fieldset className="form-group">
          <label>Type de projet</label>
          <select defaultValue="0" className="form-control">
            <option value="0" disabled>Selectionner</option>
            <option value="1">Auto-entrepreneuriat ou microactivit</option>
            <option value="2">Cration d'entreprise</option>
            <option value="3">Reprise d'entreprise</option>
            <option value="4">Cration ou transmission d'une autre forme d'activit (association et autre)</option>
            <option >Cration ou transmission d'entreprise familiale</option>
          </select>
        </fieldset>
        <fieldset className="form-group">
          <label>Etat d'avancement</label>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
               Construction
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
              Lancement
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
             Cration, reprise ou transmission ralise
            </label>
          </div>
        </fieldset>
      </div>
    )
  }
}

export default ProjectStep



