import React, { Component, PropTypes } from 'react'

class ContextStep extends React.Component {
  render() {
    return (
      <div>
        <p>Ma situation</p>
        <fieldset className="form-group">
          <label>En septembre 2016 :</label>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
               J'ai aurai fini ou arrt mes tudes
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
              Je serai tudiante
            </label>
          </div>
          <small>Ne pas prendre en compte l'inscription au diplme tudiante entrepreneure (D2E)</small>
        </fieldset>
        <p>Ici, bientt l'identit, les coordonnes...</p>
      </div>
    )
  }
}

export default ContextStep



